.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  width: min(720px, 95vw);
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.modal-header {
  padding: 1rem 1.25rem;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-body {
  padding: 1rem 1.25rem;
  max-height: 60vh;
  overflow: auto;
}

.modal-footer {
  padding: 1rem 1.25rem;
  border-top: 1px solid #e5e7eb;
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

.close {
  border: 1px solid #d1d5db;
  background: #f3f4f6;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
}

.btn {
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  background: #f3f4f6;
}

.btn.btn-secondary {
  background: #fff;
  color: #374151;
}

.summary {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-bottom: 1rem;
}

.summary .label {
  font-size: 0.8rem;
  color: #6b7280;
}

.summary .value {
  font-weight: 700;
}

.summary .total {
  color: #111827;
}

.parts-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.parts-list .row {
  display: flex;
  justify-content: space-between;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

.parts-list .name {
  font-weight: 600;
}

.parts-list .qty {
  color: #6b7280;
}

.parts-list .cost {
  font-weight: 600;
}
